.Profile {
    background-color: #edf2f6;
}

.header-profile {
    width: 95%;
    margin: 70px auto 0 auto;
    background-color: #ffffff;
    margin-bottom: 13px;

}

.header-profile::after {
    content: '';
    display: block;
    height: 0;
    clear: both;
}

.profile-bg-img::before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
}

.profile-bg-img {
    width: 100%;
    height: 300px;
    position: relative;
    margin: auto;

    background-image: url(../1_img/profile-img/profile-bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    transform-origin: center;
}

.profile-avatar {
    position: absolute;
    width: 1300px;
    height: 80px;
    bottom: 0;
    left: 50%;
    margin-left: -650px;
    z-index: 600;
}

.profile_avatar_link {
    width: 170px;
    height: 170px;

    position: absolute;
    top: 0;
    left: 0;

    border-radius: 50%;
    overflow: hidden;
}
.profile_avatar_link:before {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
}
.profile_sidebar_top {
    width: inherit;
    height: 100%;

    padding-left: 185px;
}

.profile_avatar_link img {
    position: absolute;
    width: 160px;
    height: 160px;
    top: 50%;
    left: 50%;
    margin-left: -80px;
    margin-top: -80px;
    border-radius: 50%;
}

.profile_brand-name {
    
    color: #ffffff;
    overflow: auto;
}

.profile_brand-name > img {
    width: 23px;
    height: 23px;
}

.profile_brend_social {
    width: 1300px;
    margin: auto;
}

.profile_brand_location span {
    display: inline-block;
    text-transform: uppercase;
    color: #ffffff;
    font-family: sans-serif;
    font-size: 12px;
}

.profile_brand_location span:nth-child(2) {
    margin-right: 10px;
}

.profile_brand_location  img {
    width: 14px;
    height: 14px;
    
}

.profile_sidebar_bottom {
    padding-top: 20px;
    padding-left: 185px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.socila_icons {
    display: inline-block;
}

.socila_icons li {
    display: inline-block;
    margin-right: 10px;
}

.socila_icons li a img {
    width: 35px;
    height: 35px;
}

.profile-info {
    width: 95%;
    height: 85px;
    margin: auto;
    padding-bottom: 5px;
}

.profile_rating {
    float: right;
    display: block;
    height: 100%;
}

.profile_rating::after {
    content: '';
    display: block;
    clear: both;
}

.rating_text {
    display: inline-block;
    font-family: sans-serif;
    font-size: 10px;
}

.profile_rating img {

    height: 17px;
    width: 17px;
    margin-left: 10px;
}

.rating_more-info {
    display: inline-block;
}

.rating_wrap {
    display: flex;
    align-items: center;
}
.rating_info {
    text-align: center;
    margin-left: 30px;
}

.rating_info span {
    opacity: .8;
    font-size: 20px;
    font-family: sans-serif;
}

.rating_info h3 {
    opacity: .8;
    font-weight: normal;
    font-family: sans-serif;
}

.rating_info.posts {
    margin-right: 100px;
}


.profile_slider {
    width: 100%;
    height: 250px;
}

.profile_section_container {
    width: 1300px;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.profile_slider {
    overflow: hidden;
    position: relative;
}

.img_1 {
    width: 100%;
    height: 100%;
    background-image: url(../1_img/profile-img/slide-1.jpg);
    background-position: center;
    background-size: cover;
    background-repeat:no-repeat;
}

.profile_slider_dots {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
}

.profile_slider_dots li {
    display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #fff;
    margin-right: 20px;
}

.profile_left_content {
    width: 70%;
    padding-right: 20px;
}

.profile_right_content {
    width: 30%;
}

.write_message_link {
    box-sizing: border-box;
    width: 100%;
    padding: 53px 25px;
    background-color: lightblue;
    margin-bottom: 20px;
    text-align: center;
}

.profile_login {
    position: relative;
    height: 100px;
}

.profile_login a {
    width: 100%;
    display: block;
    padding: 50px 20px;
    box-sizing: border-box;

    position: absolute;
    bottom: 0;
    left: 0;

    background-color: pink;
    text-align: center;
    overflow: hidden;
    z-index: 100;
}

.profile_login a:after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    
    position: absolute;
    top: 0;
    left: 0;

    background-image: url(../1_img/profile-img/login-bg-img.jpg);
    background-size: cover;
    background-position: left bottom;
    background-repeat: no-repeat;

    transform: scale(1.1);
    transition: .5s ease;
    z-index: 1;
}

.profile_login a:hover:after {
    transform: scale(1.2);
}

.reg_ling_profile > span {
    position: absolute;
    z-index: 100;
    display: block;
    left: 50%;
    top: 50%;
    margin-left: -43px;
    margin-top: -12px;
}