.profile_nav {
    max-width: 1300px;
    margin: auto;
    padding-bottom: 15px;
}

.profile_nav_list {
    border-radius: 3px;
    width: 100%;
    height: 107px;
    background-color: #fff;
}

.profile_nav_list li {
    position: relative;
    line-height: 107px;
    height: 100%;
    display: inline-block;
    padding: 0 25px;
    border-right: 1px solid #e6ecf5;
    cursor: pointer;
}

.profile_nav_list li.active:after {
    content: '';
    display: block;
    position: absolute;
    width: 100%;
    height: 4px;
    background-color: #ff5e3a;
    bottom: 0;
    left: 0;
}