/* NAVIGATION CSS */

nav {
    width: 100%;
    background-color: #222835;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    height: 70px;
}

.header__wrap {
    height: 100%;
    line-height: 70px;
}

.container{
    max-width: 1770px;
    height: 100%;
    padding: 0 20px;
    margin: 0 auto;
    box-sizing: border-box;
}

.menu {
    padding-left: 180px;
}

.menu > ul > li:before {
    transition: .25s;
    content: '';
    display: inline-block;
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 4px;
    top: -1px;
    left: 0;
    background-color:#0d58c8;
}


.menu > ul > li:hover a {
    color: #0d58c8;
    transition: .25s;
}

.menu > ul > li:hover:before{
    opacity: 1;
}

.menu > ul > li {
    position: relative;
    height: 70px;
    padding: 0px 40px;
    display: inline-block;
    text-transform: uppercase;
    font-size: 14px;
}

.reg {
    border-radius: 3px;
    background-color: #0d58c8;
    color: #fff;
    font-weight: bold;
    border: 1px solid #001dbf;
    transition: .3s;
}

.reg:hover {
    background: none;
    border: 1px solid #fff;
}

.reg span {
    display: inline-block;
    width: 10px;
    height: 10px;
    background-color: #fff;
    border-radius: 50%;
   margin-right: 6px;
   background-color: red;

}

.login {
    font-size: 14px;
    float: right;
    text-transform: uppercase;
}

.login a {
    padding: 8px 22px;
}

.logo {
    width: 50px;
    position: relative;
}

.brand {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -30px;
}
/* HEADER CSS */

header {
    margin-top: 70px;
}

.overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
}

.head-bg {
    width: 100%;
    height: 420px;
    background-image: url(../1_img/bg.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
}

.header_content {
    height: 400px;
    width: 100vw;
    text-align: center;
    position: relative;
    z-index: 200;
    display: table-cell;
    vertical-align: middle;
}

.header_content {
    text-align: center;
    color: #fff;
}

.links {
    display: inline-block;
    font-size: 12px;
    line-height: 12px;
    font-weight: 400;
    color: #fff;
    text-transform: uppercase;
    margin-right: 27px;
    border-radius: 3px;
    padding: 15px 28px;
    transition: .3s;
    margin-top: 17px;
 }
 .menu,
.login,
.logo {
    display: inline-block;
}
.menu-footer-nav {
    font-size: 0;
}

.menu-footer-nav .footer-menu-item {
    display: inline-block;
    width: calc(100% / 2);
    font-size: 14px;
    text-align-last: left;
}

.links-1{
    background: #0d58c8;
    border: 1px solid #0d58c8;
}

.links-1:hover {
    background: none;
}

.links-2:hover {
    background: none;
    color: #fff;
}

.links-2 {
    background: #fff;
    color: #7a8192;
    border: 1px solid #7a8192;
}

.filters {
    padding: 50px 0;
}

.filter-content {
    font-size: 0;
}

.filter_left,
.filter_right {
    display: inline-block;
    box-sizing: border-box;
    padding: 0 15px;
}

.filter_left {
    width: calc(100% / 6);
    padding-left: 0;
}

.filter_left form input {
    width: 100%;
    height: 100%;
    padding: 14px 10px;
}

.filter_right {
    width: calc(100% *5/6);
}

.filter_left form {
    width: 100%;
    height: 50px;
}

.filter_left form input {
    border: 1px solid #edeff2;
    border-radius: 3px;
    padding: 14px 25px 14px 10px;
}

.filter_left form input:focus{
    outline: none;
}

.filter_right-content {
    width: 100%;
    height: 50px;
}

.drop-down {
    border: 1px solid #edeff2;
    cursor: pointer;
    border-radius: 3px;
    display: inline-block;
    padding: 15px 80px 15px 50px;
    font-size: 13px;
    color: #858585;
    margin-right: 30px;
    transition: .3s;
}

.drop-down:hover {
    background: rgb(13, 88, 200);
    border: 1px solid #0d58c8;
    color: #fff;
}

.drop-down:last-child {
    margin: 0;
}

/* Hide the cancel button */
::-webkit-search-cancel-button { 
    -webkit-appearance: none; 
}

input[type=search]::placeholder {
    font-size: 13px;
    color: #858585;
}

