.form_wrap_reg {
    position: absolute;
    top: 70px;
    left: 50%;
    transform: translate(-50%);
    width: 100%;
    padding: 106px 0 40px 0;
    background: #1b212c;
    z-index: 700;
}

.reg_form input:nth-child(1),
.reg_form input:nth-child(2) {
    margin-bottom: 20px;
}
.reg_form input:nth-child(1),
.reg_form input:nth-child(3) {
    margin-right: 20px;
}

.reg_form {
    width: 920px;
    margin: auto;
    text-align: center;
}

.reg_form input {
   width: calc(50% - 97px);

   box-sizing: border-box;
   padding: 14px 15px;
   margin: 0;
   border: none;
}

.sumbit_wrap {
    text-align: center;
    padding-top: 20px;
}

.reg_form input[type='submit'] {
    display: inline-block;
    margin-bottom: 0;
    width: auto;

    color: #FFF;
    padding: 15px 30px;
    background: #0d58c8;
    font-size: 14px;
    border: none;
    border-radius: 2px;
}