.content_wrap {
    font-size: 0;    
}

.categories,
.content_list {
    vertical-align: top;
    width: 100%;
    padding: 0 15px;
    box-sizing: border-box;
    display: inline-block;
    font-size: 15px;
}

.categories {
    width: calc(100% / 6);
    padding-left: 0;
    text-align: center;
}

.content_list {
    width: calc(100% *5/6);
    padding-right: 0;
    padding-bottom: 50px;
}

.list_wrap ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.list_wrap .box {
    min-width: calc(100% / 5);
    box-sizing: border-box;
    padding: 11px 15px 11px 20px;
    color: #858585;
    cursor: pointer;
}

.box-item-wrap {
    display: block;
    width: 100%;
    height: 100%;
    color: #000;
}

.box-img {
    height: 195px;
    background-image: url(../1_img/item-1.jpg);
    background-position:  center;
    background-size: cover;
    background-repeat: no-repeat;
}


.categories_filter{
    background: #F6F6F6;
    display: block;
}

.categories_filter li {
    display: block;
    text-align: left;
    font-size: 13px;
    min-height: 40px;
    line-height: 40px;
    color: #858585;
    cursor: pointer;
    border: 1px solid #E6E6E6;
    border-bottom: none;
    transition: 1s;
    overflow: hidden;
}

.categories_filter li:last-child {
    border-bottom: 1px solid #E6E6E6;
}

.block {
    width: 100%;
    background-color: #fff;
    position: relative;
    z-index: 1;
    max-height: 0;
    transition: 1s max-height;
}

.block.active {
    max-height: 205px;
}

.block-list li {
    padding-left: 40px;
    border-left: none;
    border-right: none;
    transition: .3s;
}

.block-list li:hover {
   background-color: rgb(233, 233, 233);
}

.block-list li:last-child {
    border-bottom: none;
}

.vf span {
    width: 100%;
    padding-left: 13px;
    background: #F6F6F6;
    display: block;
    overflow: hidden;
    position: relative;
    z-index: 100;
}
