#Posts {
    width: 1300px;
    margin: auto;
}

.post_card {
    display: inline-block;
    width: 100%;
    background-color: #ffffff;
    margin-bottom: 25px;
    border-radius: 4px;

    padding-bottom: 10px;
    padding-left: 10px;
    box-sizing: border-box;
}

.post_card__title {
    padding: 10px 25px;
}

.post_img_wrap {    
    width: 68%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    height: 252px;
    padding: 0 20px;
    overflow-x: auto;
    overflow-y: none;
}

.post_img {
    display: inline-block;
    width: 270px;
    height: 200px;
    margin-right: 20px;
}

.post_img_1 {
    background-size: cover;
    background-image: url(../1_img/posts/item-post.jpg);

    background-position: center;
    background-repeat: no-repeat;
}

.post_right_title {
    display: inline-block;
    width: 32%;
    text-align: center;
}

.post_card_wrap {
    display: flex;
}

.post_main_card_wrap {
    padding-right: 20px;
    display: inline-block;
    width: 70%;
    box-sizing: border-box;
}

.user_reviews {
    display: inline-block;
    width: 30%;
    box-sizing: border-box;
    padding: 25px 0;
    background-color: #edf2f6;
    vertical-align: top;
}
