footer {
    background-color: #222835;
}

.footer-main {
    border-bottom: 1px solid #2c3445;
    width: 100%;
    display: flex;
    padding: 46px 0;
}

.footer_column-1,
.footer_column-2,
.footer_column-3,
.footer_column-4 {
    width: calc(100% / 4);
    padding-right: 20px;
    color: #FFF;
    font-size: 14px;
}

.footer_column-4 {
    padding-right: 0;
}

.menu-footer-nav {
    font-size: 0;
}

.menu-footer-nav .footer-menu-item {
    display: inline-block;
    width: calc(100% / 2);
    font-size: 14px;
    text-align-last: left;
}

.social-item {
    display: inline-block;
    margin-right: 15px;
}

.social-link-1 img,
.social-link-2 img,
.social-link-3 img,
.social-link-4 img {
    display: inline-block;
    width: 30px;
    height: 30px;
    fill: #fff;
}

.social-link-1 img {
    content: url('../1_img/vk-brands.svg');
}
.social-link-2 img {
    content: url('../1_img/instagram-brands.svg');
}
.social-link-3 img {
    content: url('../1_img/telegram-plane-brands.svg');
}
.social-link-4 img {
    content: url('../1_img/youtube-brands.svg');
}

.menu-footer-nav li {
    margin-bottom: 10px;
}

.menu-footer-nav li a {
    color: #bdbfc8;
}

.footer_column-1 p,
.social-wrap li,
.footer_column-4 p {
    color: #5d6372;
    font-size: 13px;
}

.bottom-left span {
    color: #a9a9a9;
    font-size: 12px;
    margin-right: 20px;
}

.footer-bottom {
    display: flex;
    justify-content: space-between;
    line-height: 80px;
}

.bottom-left span > a {
    color: #fff;
    font-size: 13px;
}

.bottom-right a {
    text-transform: uppercase;
    font-size: 10px;
    padding: 12px 19px 11px 19px;
    color: #9fa5b5;
    border: 1px solid #414856;
    border-radius: 3px;
    margin-left: 15px;
    transition: .3s;
}

.bottom-right a:hover {
    background-color: #0d58c8;
    color: #fff;
    border: 1px solid #0d58c8;
}

.subscribe-form {
    width: 100%;
    position: relative;
}

.subscribe-form input[type="email"] {
    box-sizing: border-box;
    font-size: 13px;
    width: 100%;
    border: none;
    background: transparent;
    color: #5d6372;
    padding: 15px;
    padding-right: 46px;
    border-radius: 4px;
    border: 1px solid #414856;
}

.subscribe-form input[type="submit"] {
    opacity: 0;
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1000;
    cursor: pointer;
}

.submit-wrap {
    position: absolute;
    height: 46px;
    width: 46px;
    background: #0d58c8;
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    border-radius: 4px;
}

.subscribe-form:before {
    content: url(../1_img/envelope-regular.svg);
    display: block;
    width: 22px;
    height: 22px;
    position: absolute;
    top: 50%;
    right: 0;
    z-index: 1000;
    margin-right: 11px;
    margin-top: -11px;
}

.popup-reg {
    overflow: hidden;
    display: block;
    position: fixed;
    top: 70px;
    width: 100%;
    height: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.9);
    z-index: 5000;
    transition: .7s height ease;
}

.popup-wrap {
    width: 100%;
    padding: 45px 15px;
    text-align: center;
    margin: 0 auto;
    background: #1b212c;
    padding-top: 45px;
    padding-bottom: 45px;
}

.popup-container {
    max-width: 900px;
    margin: 0 auto;
}

.popup-wrap h3 {
    color: #fff;
}


.input-signtype {
    max-width: 430px;
    width: 100%;
    line-height: 45px;
    border-radius: 2px;
    border: none;
    color: #b4b7c1;
    padding-left: 15px;
    margin-bottom: 20px;
}

.be-checkbox {
    position: relative;
    margin: 0 auto;
    max-width: 892px;
    width: 100%;
    text-align: left;
    color: #727785;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
}

.checkbox-input {
    top: 8px;
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

.check-box-sign {
    top: 8px;
    position: absolute;
    margin-top: calc(-25px / 2);
    height: 25px;
    width: 25px;
    background-color: #eee;
}

.large-popup-text {
    margin-left: 40px;
}

.checkbox-input:checked ~ .check-box-sign {
    background-color: #2196F3;
}

.check-box-sign:after {
    content: "";
    position: absolute;
    display: none;
}

.checkbox-input:checked ~ .check-box-sign:after {
    display: block;
}

.check-box-sign:after {
    left: 9px;
    top: 5px;
    width: 5px;
    height: 10px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

.be-popup-sign-button {
    color: #FFF;
    padding: 15px 30px;
    background: #0d58c8;
    font-size: 14px;
    border: none;
    border-radius: 2px;
    cursor: pointer;
}