header {
    margin-top: 70px;
}

.overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    position: absolute;
}

.head-bg {
    width: 100%;
    height: 420px;
    background-image: url(../1_img/bg.jpg);
    background-size: cover;
    background-position: center;
    position: relative;
}

.header_content {
    height: 400px;
    width: 100vw;
    text-align: center;
    position: relative;
    z-index: 200;
    display: table-cell;
    vertical-align: middle;
}

.header_content {
    text-align: center;
    color: #fff;
}


.header_content h1 {
    font-size: 2.7em;
    margin: 0;
}

.links {
    display: inline-block;
    font-size: 12px;
    line-height: 12px;
    font-weight: 400;
    color: #fff;
    text-transform: uppercase;
    margin-right: 27px;
    border-radius: 3px;
    padding: 15px 28px;
    transition: .3s;
    margin-top: 17px;
 }

.links-1{
    background: #0d58c8;
    border: 1px solid #0d58c8;
}

.links-1:hover {
    background: none;
}

.links-2:hover {
    background: none;
    color: #fff;
}

.links-2 {
    background: #fff;
    color: #7a8192;
    border: 1px solid #7a8192;
}

.filters {
    padding: 50px 0;
}

.filter-content {
    font-size: 0;
}

.filter_left,
.filter_right {
    display: inline-block;
    box-sizing: border-box;
    padding: 0 15px;
}

.filter_left {
    width: calc(100% / 6);
    padding-left: 0;
}

.filter_left form input {
    width: 100%;
    height: 100%;
    padding: 14px 10px;
}

.filter_right {
    width: calc(100% *5/6);
}

.filter_left form {
    width: 100%;
    height: 50px;
}

.filter_left form input {
    border: 1px solid #edeff2;
    border-radius: 3px;
    padding: 14px 25px 14px 10px;
}

.filter_left form input:focus{
    outline: none;
}

.filter_right-content {
    width: 100%;
    height: 50px;
}

.drop-down {
    border: 1px solid #edeff2;
    cursor: pointer;
    border-radius: 3px;
    display: inline-block;
    padding: 15px 80px 15px 50px;
    font-size: 13px;
    color: #858585;
    margin-right: 30px;
    transition: .3s;
}

.drop-down:hover {
    background: rgb(13, 88, 200);
    border: 1px solid #0d58c8;
    color: #fff;
}

.drop-down:last-child {
    margin: 0;
}

/* Hide the cancel button */
::-webkit-search-cancel-button { 
    -webkit-appearance: none; 
}

input[type=search]::placeholder {
    font-size: 13px;
    color: #858585;
}