html,body {
  padding: 0;
  margin: 0;
  background-color: #ffffff;
  width: 100%;
}
section {
  padding: 0;
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
}

li {
  list-style-type: none;
}

p, a, li, h1,h2 {
  font-family: 'Kanit', sans-serif;
  margin: 0;
  padding: 0;
}

a {
  color: #fff;
  text-decoration: none;
}

h1, h2, h3, h4 {
  margin: 0;
}